<template>
  <div class="EventListCPN" v-if="list.length > 0">
    <event-item v-for="item in list" :item="item" :key="item.id + random" />
  </div>
</template>

<script>
import EventItem from "./EventItem.vue";
import EventDetail from "./EventDetail.vue";
export default {
  name: "EventListCPN",
  data() {
    return {
      random: Date.now()
        .toString()
        .slice(-3, -1)
    };
  },
  components: {
    EventItem,
    EventDetail
  },
  props: {
    list: {
      type: Array,
      default: () => []
    }
  },
  methods: {}
};
</script>
<style scoped>
.EventListCPN {
  width: 100%;
  height: 100%;
  /* background-color: pink; */
}
</style>
