export { getEventStore, setEventStore, operation };
function getEventStore(EventStore) {
  if (localStorage.getItem("EventStore")) {
    return JSON.parse(localStorage.getItem("EventStore"));
  } else {
    setEventStore(EventStore);
    return EventStore;
  }
}
function setEventStore(EventStore) {
  localStorage.setItem("EventStore", JSON.stringify(EventStore));
}
function operation(state, callback) {
  let EventStore = getEventStore(state.EventStore);
  callback(EventStore);
  setEventStore(EventStore);
  //让state中的数据保持同步，保证各个组件中能够访问
  state.EventStore = EventStore;
}
