<template>
  <div class="EventItemCPN" @dblclick="changeOpen" :class="{ isOpen: isOpen }">
    <div class="left" @dblclick.stop="" @click.stop="changeState($event)">
      <div :class="getState"></div>
    </div>
    <div class="right">
      <p class="content" :class="{ delete: isSelect }">
        {{ item.content }}
      </p>
      <p class="date">{{ date }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "EventItemCPN",
  data() {
    return { isSelect: Boolean, isOpen: false };
  },
  props: {
    item: {
      type: Object,
      default: () => {}
    }
  },
  created() {
    //保持与store的同步
    this.isSelect = this.item.isFinish;
  },
  watch: {
    "$store.state.EventStore.preset.isAdmin"() {
      //退出管理者模式，将选中效果与原状态同步
      if (!this.$store.getters.getIsAdmin) {
        this.isSelect = this.item.isFinish;
      } else {
        //是管理者模式，先将已完成选项放入待删除区
        this.isSelect && this.$store.commit("deleteBufferAdd", this.item);
      }
    }
  },
  methods: {
    changeState(event) {
      this.isSelect = !this.isSelect;
      //根据不同模式，做不同处理
      if (this.$store.getters.getIsAdmin) {
        //Admin模式
        //根据不同的状态，执行添加/删除
        if (this.isSelect) {
          this.$store.commit("deleteBufferAdd", this.item);
        } else {
          this.$store.commit("deleteBufferSub", this.item);
        }
      } else {
        //User模式
        this.$store.dispatch("changeState", this.item);
      }
    },
    changeOpen() {
      this.isOpen = !this.isOpen;
    }
  },
  computed: {
    getState() {
      return this.isSelect ? "active" : "btn";
    },
    date() {
      let time = new Date(Number(this.item.id));
      let YYYY = time
        .getFullYear()
        .toString()
        .slice(2, 4);
      let MM = time.getMonth() + 1;
      let DD = time.getDate();
      let HH = time.getHours();
      let mm = time.getMinutes();
      let times = [YYYY, MM, DD, HH, mm];
      return times.join("-");
    }
  }
};
</script>
<style scoped>
.EventItemCPN {
  width: 0;
  height: 0;
  opacity: 0;
  margin-bottom: 8px;
  background-color: #fff;
  display: flex;
  align-items: center;
  border-radius: 6px;
  animation: scaleCPN 0.3s forwards;
  overflow: hidden;
  transition: min-height 1s;
}
@keyframes scaleCPN {
  60% {
    transform: rotateX(360deg);
    transform: rotateZ(360deg);
  }
  to {
    width: 100%;
    height: 45px;
    opacity: 1;
    transform: rotateX(360deg);
    transform: rotateZ(360deg);
  }
}
.isOpen {
  min-height: 90%;
  overflow: auto;
}
.isOpen .content {
  flex: 1;
  min-width: 90%;
  overflow: unset;
  text-overflow: unset;
  white-space: unset;
  word-wrap: break-word;
}
.left {
  max-width: 100px;
  width: 20%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.right {
  margin: 6px 0px;
  flex: 1;
  height: 100%;
  text-align: left;
  display: flex;
  flex-direction: column;
}
.btn,
.active {
  border-radius: 50%;
  border: 2px solid var(--color-tint);
  position: relative;
}
.btn {
  width: 23px;
  height: 23px;
}
.active {
  width: 0;
  height: 0;
}
.active::after {
  content: "✔";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 120%;
  height: 120%;
  border-radius: 50%;
  background-color: var(--color-tint);
  color: white;
  font-weight: bold;
  animation: scalebtn 0.3s forwards;
}
@keyframes scalebtn {
  to {
    width: 23px;
    height: 23px;
  }
}
.content {
  flex: 1;
  width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.date {
  transform: translateX(-30%);
  align-self: flex-end;
  font-size: 13px;
  color: var(--color-tint);
}
.delete {
  text-decoration: line-through;
  font-style: italic;
}
</style>
